
import { defineComponent } from 'vue'

function getRandomValue(min: number, max: number) {
  return Math.floor(Math.random() * (max - min)) + min;
}

export default defineComponent({
  props: {
    startPlayerHealth: {
      required: true,
      type: Number,
    },
    startMonsterHealth: {
      required: true,
      type: Number,
    },
  },
  data(): {
    playerHealth: number;
    monsterHealth: number;
    currentRound: number;
    winner: string | null;
    logMessages: {
      actionBy: string,
      actionType: string,
      actionValue: number,
    }[];
  } {
    return {
      playerHealth: this.startPlayerHealth,
      monsterHealth: this.startMonsterHealth,
      currentRound: 0,
      winner: null,
      logMessages: [],
    };
  },
  methods: {
    startGame() {
      this.playerHealth = this.startPlayerHealth;
      this.monsterHealth = this.startMonsterHealth;
      this.winner = null;
      this.currentRound = 0;
      this.logMessages = [];
    },
    attackMonster() {
      this.currentRound++;
      const attackValue = getRandomValue(5, 12);
      this.monsterHealth -= attackValue;
      this.addLogMessage('player', 'attack', attackValue);
      this.attackPlayer();
    },
    attackPlayer() {
      const attackValue = getRandomValue(5, 12);
      this.addLogMessage('monster', 'attack', attackValue);
      this.playerHealth -= attackValue;
    },
    specialAttackMonster() {
      this.currentRound++;
      const attackValue = getRandomValue(5, 12);
      this.addLogMessage('player', 'attack', attackValue);
      this.monsterHealth -= attackValue;
      this.attackPlayer();
    },
    healPlayer() {
      this.currentRound++;
      const healValue = getRandomValue(8, 20);
      if (this.playerHealth + healValue > 100) {
        this.playerHealth = 100;
      }
      else {
        this.playerHealth += healValue;
      }
      this.addLogMessage('player', 'heal', healValue);
      this.attackPlayer();
    },
    surrender() {
      this.winner = 'monster';
    },
    addLogMessage(who: string, what: string, value: number) {
      this.logMessages.unshift({
        actionBy: who,
        actionType: what,
        actionValue: value
      });
    },
  },
  computed: {
    monsterBarStyles() {
      if (this.monsterHealth < 0) {
        return { width: '0%' };
      }
      return { width: this.monsterHealth + '%' };
    },
    playerBarStyles() {
      if (this.playerHealth < 0) {
        return { width: '0%' };
      }
      return { width: this.playerHealth + '%' };
    },
    mayUseSpecialAttack() {
      return this.currentRound % 3 !== 0;
    }
  },
  watch: {
    playerHealth(value) {
      if(value <= 0 && this.monsterHealth <= 0) {
        this.winner = 'draw';
      } 
      else if (value <= 0) {
        this.winner = 'monster';
      }
    },
    monsterHealth(value) {
      if (value <= 0 && this.playerHealth <= 0) {
        this.winner = 'draw';
      }
      else if (value <= 0) {
        this.winner = 'player';
      }
    },
  },
});
