
import { defineComponent } from 'vue';

export default defineComponent({
  emits: ['menuItemClicked'],
  props: {
    text: {
      type: String,
      default: '',
    },
  },
  methods: {
    menuItemClicked() {
      this.$store.dispatch('toggleStartMenu');
      this.$emit('menuItemClicked');
    },
  },
});
