
import { defineComponent } from 'vue';
import BoogleLogo from './BoogleLogo.vue';

export default defineComponent({
  components: {
    BoogleLogo,
  },
  data(): {
    value: string;
    searchOpen: boolean;
    searchedWord: string;
  } {
    return {
      value: '',
      searchOpen: false,
      searchedWord: '',
    };
  },
  methods: {
    searchAction(searchValue: string) {
      if (searchValue !== '') {
        this.searchOpen = true;
        this.searchedWord = searchValue;
      } else {
        this.searchOpen = false;
        this.searchedWord = '';
      }
    },
  },
});
